
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { Header } from "@/shared/type/service-header";
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';

export default defineComponent({
    components: { Pagination, VueDatePicker },
    data() {
        return {
            stopChequePaymentReportData: [] as StopChequePaymentReportType[],
            COMMON_KEY_VALUE,
            header: [
                { columnName: 'No' },
                { columnName: 'Reference No' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'Account Number ' },
                { columnName: 'Fix Deposit Type' },
                { columnName: 'Condition' },
                { columnName: 'Principle Amount' },
                { columnName: 'Currency' },
                { columnName: 'Tenor ' },
                { columnName: 'Maturity Date' },
                { columnName: 'Interest Rate' },
                { columnName: 'Payout Account' },
                { columnName: 'Remark' },
                { columnName: 'FD Account Status' },
                { columnName: 'Authorization Status code' },
                { columnName: 'Create By' },
                { columnName: 'Create Date ' },
                { columnName: 'Authorization By' },
                { columnName: 'Authorization Date' },
            ],
            totalItems: 0,
            pageSize: PAGE_SIZE,
            currentPage: 1,
            startingIndex: 1,
            isResetPage: false,
            searchValueBy: {
                date: new Date(),
            }
        }
    },
    mounted() {
        this.getStopChequePaymentReportData();
    },
    computed: {
        formattedDate(): string {
            return this.$datetime.formatVueDatePicker(this.searchValueBy.date);
        },
    },
    methods: {
        async getStopChequePaymentReportData() {
            const requestBody: RPT202001SS10_REQ_BODY = {
                pageNumber: this.currentPage,
                pageSize: PAGE_SIZE,
                searchValue: this.searchValueBy.date
            }
            const responseData = await this.$request.getRequest('RPT20500111', requestBody, true) as RPT202001SS10_RES_BODY;
            if (responseData.header.result) {
                this.stopChequePaymentReportData = this.onMappingTableData(responseData.body.list);
                console.log(responseData.body.list);
                this.totalItems = responseData.body.totalCount;
                console.log(this.stopChequePaymentReportData);
            }
        },

        onMappingTableData(lists) {
            return lists.map((item, index) => ({
                no: index + 1,
                serverTransactionID: item.serverTransactionID,
                accountNo: item.accountNo,
                customerNo: item.customerNo,
                customerName: item.customerName,
                fixDepositType: item.fixDepositType,
                depositRolloverDivideCode: item.depositRolloverDivideCode,
                termDepositAmount: item.termDepositAmount,
                currencyCode: item.currencyCode,
                contractMonths: item.contractMonths,
                maturityDate: this.$datetime.formatStringToStringDate(item.maturityDate),
                applyInterestRate: item.applyInterestRate,
                sourceOfFundAccountNo: item.sourceOfFundAccountNo,
                remark: item.remark,
                fDAccountStatus: item.fDAccountStatus,
                corpBankingApprovalStatusCode: item.corpBankingApprovalStatusCode,
                firstRegisterTellerID: item.firstRegisterTellerID,
                firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                approverID: item.approverID,
                authorizationDate: this.$datetime.formatStringToStringDate(item.authorizationDate),
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.getStopChequePaymentReportData();

        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleSearch() {
            if (this.currentPage > 1) {
                this.currentPage = 1;
                this.isResetPage = true;
                this.getStopChequePaymentReportData();
            } else {
                this.isResetPage = true;
                this.getStopChequePaymentReportData();
            }
        },

        handleReset() {
            this.searchValueBy.date = new Date();
            this.currentPage = 1;
            this.isResetPage = true;
            this.getStopChequePaymentReportData();
        }

    }
});
interface StopChequePaymentReportType {
    no: string;
    serverTransactionID: string;
    bizCID: string;
    bizName: string;
    customerNo: string;
    customerName: string;
    accountNo: string;
    fixDepositType: string;
    depositRolloverDivideCode: string;
    termDepositAmount: string;
    currencyCode: string;
    contractMonths: string;
    maturityDate: string;
    applyInterestRate: string;
    sourceOfFundAccountNo: string;
    remark: string;
    fDAccountStatus: string;
    corpBankingApprovalStatusCode: string;
    firstRegisterTellerID: string;
    firstRegisterDate: string;
    approverID: string;
    authorizationDate: string;
}

interface RPT202001SS10_REQ_BODY {
    pageNumber;
    pageSize;
    searchValue;
}

interface RPT202001SS10_RES_BODY {
    header: Header
    body: {
        totalCount: number;
        list: StopChequePaymentReportType[];
    }
}

